.loader.is-hidden, .is-hidden {
  display: none;
}

.cat-info {
  gap: 15px;
  padding-top: 15px;
  display: flex;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px dotted #000;
  border-radius: 50%;
  margin: 15px;
  animation: rotation 2s linear infinite;
  display: inline-block;
  position: relative;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

