.loader.is-hidden,
.is-hidden {
    display: none;
}

.cat-info {
    display: flex;
    gap: 15px;
    padding-top: 15px;
}

.loader {
    margin: 15px;
    width: 48px;
    height: 48px;
    border: 5px dotted black;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    box-sizing: border-box;
    animation: rotation 2s linear infinite;
  }
  
  @keyframes rotation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  } 